<template>
  <div class="garage-services">
    <header
      class="text-center d-flex garage-header bg-light p-2 justify-content-between align-center shadow-sm"
    >
      <h5 class="text-center">Bills</h5>
      <img
        class="img-fluid"
        src="@/assets/bills.png"
        alt="garage-bills-image"
      />
    </header>
    <loader :loading="loading" />
    <keep-alive>
      <main>
        <div class="garage-services-lists">
          <div class="service-list-group"></div>
          <p v-if="bills" class="text-center my-3">
            Bills Found : {{ bills.length }}
          </p>
          <ol class="list-group">
            <router-link
              class="list-group-item py-2"
              tag="a"
              :to="'/bills/' + bill.billNumber"
              v-for="bill in bills"
              :key="bill._id"
            >
              <div class="d-flex justify-content-between align-items-center">
                <div class="fw-bold number-plate">
                  <img
                    src="@/assets/file-text.svg"
                    class="img-fluid me-2 icon"
                    :alt="bill._id"
                  />
                  {{ bill.billNumber }}
                  <span
                    v-if="bill.billAmount"
                    class="ms-2 number-plate fw-normal"
                  >
                    - Rs.{{ bill.billAmount }}
                  </span>
                </div>
                <div class="p-1">
                  <img
                    class="img-fluid icon icon-normal"
                    src="@/assets/next.svg"
                    alt="delete-icon"
                  />
                </div>
              </div>
            </router-link>
          </ol>
        </div>
      </main>
    </keep-alive>
  </div>
</template>

<script>
import Loader from "../../components/Loader.vue";
import http from "../../common/http-common";
export default {
  components: {
    Loader,
  },
  data() {
    return {
      loading: false,
      bills: [],
    };
  },
  async created() {
    this.loading = true;
    const result = await http.get("/bills");
    this.bills = result.data.bills;
    this.loading = false;
  },
};
</script>

<style lang="scss" scoped>
.garage-header {
  img {
    width: 2rem;
    height: 2rem;
  }
}
.number-plate {
  font-family: monospace;
}

.icon {
  width: 20px;
  height: auto;
  &-normal {
    opacity: 0.5;
  }
}

.service-list {
  padding: 0.75rem;
  border-radius: 0.5rem;
  cursor: pointer;
  text-decoration: none;
  color: #232526;
  &:hover {
    background: rgb(240, 240, 240);
  }
  &-group {
    border-radius: 0.5rem;
  }
  &-heading {
    margin-bottom: 0.5rem;
    padding: 0.75rem;
    background: #0d6efd;
    color: #fff;
    border-radius: 0.5rem;
    font-weight: bold;
  }
}
</style>
